#root {
  margin-top: 50px;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
  justify-content: center;
  background: linear-gradient(#e3f0f1, #dae4e2); }

.game {
    text-align: center; }

label {
    font-weight: 500;
    display: block;
    &::before {
        content: "- "; }
    input {
        display: inline-block;
        margin: 5px;
        border-radius: 2px; } }
.submitButton {
    font-weight: 500;
    padding: 5px 10px;
    font-size: 16px;
    background: #a3f1d7; }


.bingoBlock {
  width: 50px;
  height: 50px;
  font-size: 25px;
  cursor: pointer;
  user-select: none;
  border: solid 1px #72b8d6;
  color: #010201;
  background-color: #90d7eb;
  &:hover {
      filter: brightness(90%); } }


.bingoClick {
    // filter: brightness(50%)
    position: relative;
    cursor: default;
    &:hover {
        filter: brightness(50%); }
    &:after {
        content: "X";
        position: absolute;
        height: 100%;
        font-size: 40px;
        color: rgba(211, 76, 23, 0.8);
        display: flex;
        align-items: center;
        opacity: 1; }
    &.active {
        color: #90d7eb;
        background: #2c2c10; } }

.bingoRow {
  display: flex;
  flex-direction: row; }

.bingoBoard {
    display: inline-block;
    margin: 20px;
    filter: brightness(70%);
    color: #0f184e;
    font-size: 14px;
    .bingoPlayerText {
        width: 100%;
        .playerNumber {
            display: inline-block;
            width: 40%; }
        .playerScore {
            width: 60%;
            text-align: center;
            display: inline-block;
            &::after {
                content: "You Win!";
                color: red;
                font-weight: 500;
                display: none; }
            &.active::after {
                display: inherit; } } } }

.bingoPlayerTurn {
    filter: inherit; }

.bingoPlayerWin {
    filter: inherit;
    box-shadow: 0px 0px 10px 0px; }


